import { Component, OnInit } from '@angular/core';
import io from 'socket.io-client';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.css']
})
export class ChatComponent implements OnInit {
  txtMessage ='';
  messages : string[] = [
    'Hi there',
    'Hello',
    'How are you'
  ];
  socket = io('https://testchat1003.herokuapp.com/');//http://localhost:3000
  constructor() { }

  ngOnInit() {
    this.socket.on('SERVER_SEND_MESSAGE', message =>{
      this.messages.push(message);
    })
  }

  SendMessage(){
    //alert(this.txtMessage);
    //this.messages.push(this.txtMessage);
    this.socket.emit('CLIENT_SEND_MESSAGE', this.txtMessage);
    this.txtMessage = '';
  }

}



